@import url("https://fonts.googleapis.com/css2?family=Quicksand&display=swap");
html, body {
  margin: 0;
}

nav {
  height: 100px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  overflow: hidden;
  position: fixed;
  top: 0;
  width: 100vw;
  background-color: rgba(255, 255, 255, 0.8);
}

nav #nav-logo img {
  height: 100%;
  margin-left: 2vw;
}

nav #nav-link {
  margin-right: 2vw;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: right;
      -ms-flex-pack: right;
          justify-content: right;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

nav #nav-link ul {
  list-style: none;
  font-size: 20px;
}

nav #nav-link li {
  display: inline;
  padding-right: 2vw;
}

nav #nav-link a {
  text-decoration: none;
  color: black;
  text-transform: uppercase;
}

nav #nav-link div {
  font-size: 30px;
}

main {
  margin-top: 100px;
  margin-bottom: 100px;
}

#home-banner {
  max-height: 566px;
  max-width: 1198px;
  margin: 0 auto;
  overflow: hidden;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  place-items: center;
}

#home-banner img {
  height: 100%;
  width: 100%;
  margin: 0 auto;
  -webkit-transition: all .3s ease-in-out;
  transition: all .3s ease-in-out;
}

