#main-gallery {
  text-align: center;
  padding-right: -10px;
}

.img-container {
  height: 367px;
  width: 279px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  overflow: hidden;
  padding-right: 44px;
}

.img-container img {
  width: 100%;
  height: 100%;
}

.card-container {
  display: inline-block;
}

.card-container p {
  text-align: left;
  text-transform: uppercase;
  font-family: 'Quicksand', sans-serif;
  padding-bottom: 40px;
  color: #838080;
  font-size: 14px;
}

footer {
  text-align: center;
  font-size: 14px;
}

footer img {
  height: 200px;
  display: block;
  margin: 0 auto;
}

footer a {
  color: black;
  text-decoration: none;
}

footer a:hover {
  text-decoration: underline;
}

footer div {
  margin-top: 14px;
}

footer div img {
  width: 24px;
  height: auto;
  display: inline;
  padding-right: 16px;
}
